html {
  scroll-behavior: smooth;
  overflow-x: hidden; }

.ravi {
  height: 100vh;
  width: 100vw;
  background: #000000e3;
  position: fixed;
  z-index: 100; }

.landingPage-container {
  text-align: center;
  cursor: default; }
  .landingPage-container p {
    line-height: 25px !important; }
  .landingPage-container #landingPage-bgVideo {
    position: fixed;
    right: 0;
    bottom: 0;
    min-width: 100%;
    min-height: 100%; }
  .landingPage-container .profile {
    text-align: center; }
    .landingPage-container .profile .video-effect {
      filter: brightness(0.8); }
    .landingPage-container .profile__title {
      margin-bottom: 20px; }
      .landingPage-container .profile__title h1 {
        color: #2297ff82;
        font-family: "Roboto Slab", "Times New Roman", serif;
        font-weight: 700;
        text-decoration: none;
        animation: animate-title 1.5s ease-in-out 0s infinite alternate-reverse;
        animation-play-state: 1; }
    .landingPage-container .profile__description {
      color: #b5b5b5;
      font-weight: 100;
      animation: animate-description 1.5s ease-in-out 0s infinite alternate-reverse;
      animation-play-state: 1; }
    .landingPage-container .profile__image img {
      opacity: 1;
      border-radius: 10em;
      object-fit: cover;
      object-position: 100% 0;
      width: 200px;
      height: 200px; }

@media screen and (max-width: 767px) {
  .profile__title h1 {
    font-size: 35px; }
  .profile__image img {
    width: 200px;
    height: 200px; }
  .profile__image .back {
    display: none; } }

@media screen and (min-width: 768px) {
  .profile__image #f1_container {
    position: relative;
    margin: 10px auto;
    width: 200px;
    height: 200px;
    z-index: 1; }
  .profile__image #f1_container {
    perspective: 1000; }
  .profile__image #f1_card {
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
    transition: all 0.5s linear; }
  .profile__image #f1_container:hover #f1_card {
    transform: rotateY(180deg); }
  .profile__image .face {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden; }
  .profile__image .face.back {
    display: block;
    transform: rotateY(180deg);
    box-sizing: border-box;
    color: white;
    text-align: center; }
  .profile__title h1 {
    font-size: 35px; }
  .profile__description {
    font-size: 20px; }
  .profile__image img {
    width: 200px;
    height: 200px; } }

@keyframes animate-title {
  from {
    opacity: 0.2; }
  to {
    opacity: 1; } }

@keyframes animate-description {
  from {
    opacity: 1; }
  to {
    opacity: 0.2; } }
